<template>
  <div
    style="position: relative; flex-grow: 1; overflow: hidden"
    @mouseenter="mouseenterFun"
    @mouseleave="mouseleaveFun"
  >
    <transition name="slide-fade">
      <img
        class="leftArrow"
        src="@/assets/home_view/arrow.png"
        alt=""
        @click="arrowFun('left')"
        v-show="showArrowLeft"
      />
    </transition>
    <transition name="slide-fade">
      <img
        class="rightArrow"
        src="@/assets/home_view/arrow.png"
        alt=""
        @click="arrowFun('right')"
        v-show="showArrowRight"
      />
    </transition>
    <div
      :class="['fdzlContet', { fdzlCenter: genetateArry.length < 2 }]"
      id="box"
      :style="{ position: 'relative', left: boxMarginLeft + '%' }"
    >
      <div v-for="(item, index) in genetateArry" :key="index">
        <div class="fdContent">
          <div class="fdY">
            <div :class="{ isCurrentClass: index == current }">
              <span style="display: inline-block; width: 0.5rem">
<!--                  {{item.pvStationName}}-->
              光伏旺旺
              </span>

            </div>
          </div>
          <div class="fdYC">
            <div>
              <p>今日电量</p>
              <p>
<!--                <span>{{ toFixedTwo(item.todayElec) }}</span> kWh-->
                <span>1230</span>kWh
              </p>
            </div>
            <div>
              <p>昨日电量</p>
              <p>
<!--                <span>{{ toFixedTwo(item.yestodayElec) }}</span> kWh-->
                <span>2230</span>kWh
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { homeGenetate } from "../../../network/home_view";
export default {
  data() {
    return {
      genetateShift: {},
      genetateArry: [],
      showArrowLeft: false,
      showArrowRight: false,
      fdzlArry: [],
      environmentalmsg: {},
      boxMarginLeft: "0",
      current: 1,
    };
  },
  mounted() {
    this.genetateFun();
  },
  methods: {
    /**
     * 切换的arrow
     * **/
    mouseenterFun() {
      // 如果只有一条数据的话不显示
      if (this.genetateArry.length < 2) {
        return;
      }
      if (this.current == 0) {
        this.showArrowLeft = true;
        return;
      }
      if (this.current == this.genetateArry.length - 1) {
        this.showArrowRight = true;
        return;
      }
      this.showArrowLeft = true;
      this.showArrowRight = true;
    },
    mouseleaveFun() {
      if (this.genetateArry.length < 2) {
        return;
      }
      this.showArrowLeft = false;
      this.showArrowRight = false;
    },
    /**
     * 获取发电总览数据
     *
     * **/
    genetateFun() {
      // this.$parent.$parent.changeGenetateLoad(true);
      //   const { errcode, errmsg, content } = res;
        let errcode = 0
        if (errcode == 0) {
            this.genetateShift = content;
            this.$bus.$emit("sendGenetateShift", this.genetateShift); //向父组件传递数据
            // this.genetateArry = content.pvStations;
            if (this.genetateArry.length == 1) {
                this.current = 0;
            } else {
                this.current = 1;
            }
            this.$bus.$emit(
                "generaMsg",
                this.genetateArry[this.current].companyId
            );
        } else {
        }
        this.$parent.$parent.changeGenetateLoad(false);
      // homeGenetate()
      //   .then((res) => {
      //     const { errcode, errmsg, content } = res;
      //     if (errcode == 0) {
      //       this.genetateShift = content;
      //       this.$bus.$emit("sendGenetateShift", this.genetateShift); //向父组件传递数据
      //       this.genetateArry = content.pvStations;
      //       if (this.genetateArry.length == 1) {
      //         this.current = 0;
      //       } else {
      //         this.current = 1;
      //       }
      //       // let a =a.toString(this.genetateArry)
      //       // console.log("!!!!!!!!!!!!!"+a)
      //       this.$bus.$emit(
      //         "generaMsg",
      //         this.genetateArry[this.current].companyId
      //       );
      //     } else {
      //     }
      //     this.$parent.$parent.changeGenetateLoad(false);
      //   })
      //   .catch((err) => {
      //     this.$parent.$parent.changeGenetateLoad(false);
      //   });
    },
    /**
     * 箭头切换事件
     * **/
    arrowFun(_direction) {
      //根据向左向右处理
      if (_direction == "left") {
        this.boxMarginLeft = Number(this.boxMarginLeft - 33.33);
        setTimeout(() => {
          this.current++;
          this.showOrHide();
        }, 300);
      } else {
        this.boxMarginLeft = Number(this.boxMarginLeft + 33.33);
        setTimeout(() => {
          this.current--;
          this.showOrHide();
        }, 300);
      }
    },
    showOrHide() {
      if (this.current == 0) {
        this.showArrowLeft = true;
        this.showArrowRight = false;
      } else if (this.current == this.genetateArry.length - 1) {
        this.showArrowRight = true;
        this.showArrowLeft = false;
      } else {
        this.showArrowLeft = true;
        this.showArrowRight = true;
      }
      // this.$bus.$emit("generaMsg", this.genetateArry[this.current].companyId);
    },
    /**
     * 数据保留两位小数
     * **/
    toFixedTwo(val) {
      if (val == 0) {
        return 0;
      }
      if (val) {
        //1.先判断是不是整数，如果不是整数则保留两位小数
        let num = val;
        if (!this.$isInteger(val)) {
          num = num.toFixed(2);
        }
        return num;
      }
    },
  },
};
</script>
<style scoped lang="less">
.slide-fade-enter-active {
  transition: all 0.2s ease;
}
.slide-fade-leave-active {
  transition: all 0.2s cubic-bezier(1, 0.5, 0.8, 1);
}
.slide-fade-enter,
.slide-fade-leave-to {
  transform: translateX(5px);
  opacity: 0;
}
.leftArrow {
  position: absolute;
  width: 0.25rem;
  top: 0.68rem;
  left: -3px;
  transform: translateY(-50%);
  cursor: pointer;
  z-index: 9;
}
.rightArrow {
  position: absolute;
  width: 0.25rem;
  top: 0.68rem;
  right: -4px;
  transform: translateY(-50%) rotate(180deg);
  cursor: pointer;
  z-index: 9;
}
.fdContent {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.fdzlContet {
  display: flex;
  height: 100%;
  transition: all 0.3s linear;
}
.fdzlContet > div {
  width: 33.33%;
  flex-shrink: 0;
}
.fdY {
  display: flex;
  justify-content: center;
  position: relative;
  width: 100%;
  height: 1.36rem;
  align-items: center;
}
.fdY > div {
  width: 0.9rem;
  height: 0.9rem;
  background: url("@/assets/home_view/yuan1.png");
  background-size: 0.9rem 0.9rem;
  flex-shrink: 0;
  background-repeat: no-repeat;
  background-position: center;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.16rem;
  font-weight: bold;
}
