//   height: 1.36rem;
//   background: url("@/assets/home_view/yuan.png");
//   background-size: 100% 100%;
//   flex-shrink: 0;
// }
.fdYC {
  display: flex;
  flex-wrap: wrap;
  flex-grow: 1;
  margin-left: 0.13rem;
  > div {
    width: 100%;
    font-size: 0.14rem;
    margin: 0.15rem 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border-left: 0.05rem solid #00c8ff;
    padding: 0.03rem 0;
    padding-left: 0.07rem;
    box-sizing: border-box;
    span {
      color: #00c8ff;
    }
    p {
      white-space: nowrap;
      text-align: left;
    }
  }
}
.fdYC > div:nth-of-type(2) {
  border-left: 0.05rem solid #00ff2a;
  span {
    color: #00ff2a;
  }
}
.isCurrentClass {
  width: 1.36rem !important;
  height: 1.36rem !important;
  background: url("@/assets/home_view/yuan.png") !important;
  background-size: 100% 100% !important;
  flex-shrink: 0 !important;

  -webkit-animation: rotation 15s linear infinite;
  animation: rotation 15s linear infinite;
}
@keyframes rotation {
  0% {
    -webkit-transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
  }
}
//旋转相抵 子元素取消旋转
.isCurrentClass span {
  -webkit-animation: rotation1 15s linear infinite;
  animation: rotation1 15s linear infinite;
}
@keyframes rotation1 {
  0% {
    -webkit-transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(-360deg);
  }
}
.fdzlCenter {
  justify-content: center;
}
</style>