<template>
  <div class="home_view">
    <div class="particle"></div>
    <div class="left">
      <div class="box_border">
        <load-box :loadisShow="areaEnergyLoad">
          <div class="full">
            <p class="box_title">全厂用能总览</p>
            <div class="box-content" style="display: flex">
