                <div style="height: calc(1.38rem + 2px); padding: 0"></div>
                <div>单位</div>
                <div>当日</div>
                <div>昨日</div>
                <div>环比</div>
              </div>
              <div class="qcynContent">
                <div class="line1">
                  <div
                    v-for="(item, i) in 8"
                    :key="i"
