                />
                <p>光伏总容量</p>
<!--                <div>{{ genetateShift.zInstallCapacity }}kWp</div>-->
                <div>8056.08kWp</div>
                <p>当日发电量</p>
<!--                <div>{{ genetateShift.zeToday }}kWh</div>-->
