                      "
                      >
<!--                      {{ environmentalmsg.reduceCarbonDioxide }}-->
                      210
                    </span>
                    吨
                  </p>
                </div>
              </div>
              <div>
                <div>
                  <img src="@/assets/home_view/tp2.png" alt="" />
                </div>
                <div class="TPJSContent">
                  <p>减排二氧化硫</p>
