  ElecEqmId,
} from "../../network/home_view";
export default {
  components: {
    energyConsumptionChart,
    generatingOverview,
    generatingOverview2,
    loadBox,
  },
  data() {
    return {
      options: [1,2,3,4],
      value: "",
      eqmOptions: [1,2,3,4],
      eqmValue: "",
      activeIndex: 0,
      elecpoinArry: [],
      elecpoinNameArry: [
        "总电能",
        "A相电压",
        "B相电压",
        "C相电压",
        "A相电流",
        "B相电流",
        "C相电流",
        "A相功率",
        "B相功率",
        "C相功率",
      ],
      topId: "",
      ynIcon: ["电", "蒸汽", "天然气", "自来水", "冷凝水", "压缩空气", "氮气","中水"],
      ynUnit: ["kWh", "吨", "立方米", "立方米", "立方米", "立方米", "立方米", "立方米"],
      ynArry: [
        {
          当日: ["-", "-", "-", "-", "-", "-", "-", "-"],
        },
        {
          昨日: ["-", "-", "-", "-", "-", "-", "-", "-"],
        },
        {
          环比: ["-", "-", "-", "-", "-", "-", "-", "-"],
        },
