      areaUseQtyArry: [],
      genetateShift: {},
      environmentalmsg: {},
      areaEnergyLoad: false,
      areaUseQtyLoad: false,
      chLoad: false,
      genetateLoad: false,
      tpLoad: false,
      eleLoad: false,
      topIdNum: "",
      companyId: sessionStorage.getItem("companyId"),
      s1:[0.2,0.3,0.8,0.6,0.7,0.9,0.7,0.55,0.1,0.8,0.22,0.32,0.66,0.58,0.14,0.96,0.1,0.3,0.6,0.44],
      s2:[0.6,0.1,0.7,0.4,0.5,0.15,0.33,0.6,0.8,0.12,0.3,0.1,0.36,0.8,0.4,0.1,0.66,0.55,0.4,0.8],
      pieData: [],
