    };
  },
  created() {
    this.$bus.$on("sendGenetateShift", (msg) => {
      this.genetateShift = msg;
    });
  },
  mounted() {

    particleBg(".particle", {
      color: "rgb(53,115,229,0.5)",
      radius: 2,
      rate: 2,
      count: 200,
      line: false,
      bounce: true,
    });
    // window.addEventListener("keydown", this.handleEvent);
    this.areaEnergyFun();
    this.changeEnergy(0);
    this.getTopIdFun();
    // this.areaUseQtyFun();
    this.environmentalProtectionContributionFun(); //获取碳排建设

    this.elecstationFun(); //获取电站

    //监听窗口的变化 重新加载中间拓扑图
    window.onresize = () => {
      const htmlWidth =
        document.documentElement.clientWidth || document.body.clientWidth;
      const htmlDom = document.getElementsByTagName("html")[0];
      htmlDom.style.fontSize = htmlWidth / 19.2 + "px"; //根据设计稿转换单位1rem = 100px

      if (this.topIdNum != "") {
        this.topId = "";
        setTimeout(() => {
          this.topId =
            "http://" +
            this.$ipAddress +
            "/html/apps/jingheforWebpage/index.html?topId=" +
            this.topIdNum;
        }, 100);
      }
    };
  },
  methods: {

    /**
     * 区别判断用能数据中的环比
     * **/
    getYnData(data) {
      if (data.toString().includes("%")) {
        let num = data.substring(0, data.toString().indexOf("%"));

        if (!this.$isInteger(num)) {
          num = Number(num).toFixed(2);
        }
        if (1 > 0) {
          return `<span style="display: flex;align-items: center;"><img class="downImg" style="width:0.2rem" src=${down}>${num}%</span>`;
        } else {
          return `<img class="upImg" style="width: 0.1rem;margin-right: 0.03rem;" src=${up}>${num}%`;
        }
      } else {
        if (this.$isNumber(data)) {
          /**
           * 如果不是整数的话保留两位小数
           * **/
          let data1 = data;
          if (!this.$isInteger(data)) {
            data1 = data.toFixed(2);
          }
          return data1;
        } else {
          return data;
        }
      }
    },
    /**
     * 获取用能总览数据
     * **/
    areaEnergyFun() {
      // this.areaEnergyLoad = true;
      this.ynArry = [
        {
          当日: [
            153,
            132,
            100,
            74,
            68,
            63,
            25,
            44
          ],
        },
        {
          昨日: [
            125,
            63,
            126,
            39,
            85,
            36,
            66,
            55
          ],
        },
        {
          环比: [
            -0.18,
            -0.52,
            0.26,
            -0.47,
            0.25,
            0.28,
            0.11,
            -0.31
          ],
        },
      ];
      // areaEnergy()
      //   .then((res) => {
      //     const { errcode, errmsg, content } = res;
      //     if (errcode == 0) {
      //       this.ynArry = [
      //         {
      //           当日: [
      //                   153,
      //                   132,
      //                   100,
      //                   74,
      //                   68,
      //                   63,
      //                   25,
      //                   44
      //             // content.elec1,
      //             // content.steam1,
      //             // content.gas1,
      //             // content.cold1,
      //             // content.hot1,
      //             // content.compressedAir1,
      //             // content.n21,
      //             // content.reclaimedWater1,
      //           ],
      //         },
      //         {
      //           昨日: [
      //                   125,
      //                   63,
      //                   126,
      //                   39,
      //                   85,
      //                   36,
      //                   66,
      //                   55
      //             // content.elec2,
      //             // content.steam2,
      //             // content.gas2,
      //             // content.cold2,
      //             // content.hot2,
      //             // content.compressedAir2,
      //             // content.n22,
      //             // content.reclaimedWater2,
      //           ],
      //         },
      //         {
      //           环比: [
      //                   -0.18,
      //                   -0.52,
      //                   0.26,
      //                   -0.47,
      //                   0.25
      //             // content.elecQOQ,
      //             // content.steamQOQ,
      //             // content.gasQOQ,
      //             // content.coldQOQ,
      //             // content.hotQOQ,
      //             // content.compressedAirQOQ,
      //             // content.n2QOQ,
      //             // content.reclaimedWaterQOQ,
      //           ],
      //         },
      //       ];
      //     } else {
      //     }
      //     this.areaEnergyLoad = false;
      //   })
      //   .catch((err) => {
      //     this.areaEnergyLoad = false;
      //   });
    },
    /**
     * 生成分时用量
     * **/
    setFsylEchart(x,s1,s2, unit) {
      let chart = this.$refs.fsyl;
      let myChart = this.$echarts.init(chart);
      myChart.clear();
      let option = {
        grid: {
          left: "2%",
          right: "2%",
          bottom: "3%",
          containLabel: true,
        },
        legend: {
          top: "2%",
          right: "2%",
          textStyle: {
            color: "#fff",
          },
        },
        tooltip: {
          show: true, //---是否显示提示框,默认为true
          trigger: "item", //---数据项图形触发
        },
        xAxis: {
          type: "category",
          data: x,
          axisLabel: {
            show: true,
            textStyle: {
              color: "#ffffff",
            },
          },
          axisTick: {
            show: false,
          },
        },
        yAxis: {
          name: unit,
          nameLocation: "end",
          nameTextStyle: {
            color: "#fff",
            padding: [0, 10, 0, 0],
          },
          nameGap: 15,
          type: "value",
          axisLabel: {
            show: true,
            textStyle: {
              color: "#ffffff",
            },
          },
          splitLine: {
            show: true,
            lineStyle: {
              color: ["rgba(255, 255, 255, 0.5)"],
              type: "dashed",
            },
          },
        },
        series: [
          {
            name: "今日",
            type: "bar",
            // stack: "total",
            emphasis: {
              focus: "series",
            },
            barWidth: 5, //柱图宽度
            itemStyle: {
              normal: {
                color: "#00EEFF",
              },
            },
            data: this.s1,
            // data:[0.2,0.3,0.8,0.6,0.7,0.9,0.7,0.55,0.1,0.8],
          },
          {
            name: "昨日",
            type: "bar",
            // stack: "total",
            emphasis: {
              focus: "series",
            },
            barWidth: 5, //柱图宽度
            itemStyle: {
              normal: {
                color: "#FFB300",
              },
            },
            data:this.s2,
            // data: [0.6,0.1,0.7,0.4,0.5,0.15,0.33,0.6,0.8,0.12]
          },
        ],
      };
      myChart.setOption(option);
      window.addEventListener("resize", function () {
        myChart.resize();
      });
    },
    /**
     * 获取图的接口
     * **/
    getTopIdFun() {
      getTopId()
        .then((res) => {
          const { errcode, errmsg, topId } = res;
          if (errcode == 0) {
            if (topId != "") {
              this.topId =
                "http://" +
                this.$ipAddress +
                "/html/apps/jingheforWebpage/index.html?topId=" +
                topId;
              this.topIdNum = topId;
            }
          } else {
          }
        })
        .catch((err) => {});
    },
    /**
     * 获取分时用量
     * **/
    // areaUseQtyFun() {
    //   this.areaUseQtyLoad = true;
    //   areaUseQty()
    //     .then((res) => {
    //       const { errcode, errmsg, content } = res;
    //       if (errcode == 0) {
    //         this.areaUseQtyArry = content.arr;
    //         this.changeEnergy(0); //默认显示电的全厂分时用量
    //       } else {
    //       }
    //       this.areaUseQtyLoad = false;
    //     })
    //     .catch((err) => {
    //       this.areaUseQtyLoad = false;
    //     });
    // },
    /**
     *通过选择用能总览
     *切换全厂分时用量
     * **/
    changeEnergy(i) {
      this.activeIndex = i;
      let keys = [
        ["elec"],
        ["steam"],
        ["gas"],
        ["coldWater"],
        ["hotWater"],
        ["compressedAir"],
        ["n2"],
        ["reclaimedWater"],
      ];
      let units = [
        "kWh",
        "吨",
        "立方米",
        "立方米",
        "立方米",
        "立方米",
        "立方米",
        "立方米",
      ];
      let key = keys[i];
      let xAxisData = [];
      let seriesData1 = [];
      let seriesData2 = [];
      this.areaUseQtyArry.forEach((item) => {
        if (key.length == 1) {
          seriesData1.push(item[key[0]]);
          seriesData2.push(item[key[0] + "_3"]);
        } else {
          seriesData1.push(Number(item[key[0]]) + Number(item[key[1]]));
          seriesData2.push(
            Number(item[key[0] + "_3"]) + Number(item[key[1] + "_3"])
          );
        }
        xAxisData.push(item.dateStr);
      });
      this.setFsylEchart(xAxisData, seriesData1, seriesData2, units[i]);
    },
    /**
     *
     *获取碳排建设数据
     * **/
    environmentalProtectionContributionFun() {
      this.tpLoad = true;
      environmentalProtectionContribution()
        .then((res) => {
          const { errcode, errmsg, content } = res;
          if (errcode == 0) {
            this.environmentalmsg = content;
          } else {
          }
          this.tpLoad = false;
        })
        .catch((err) => {
          this.tpLoad = false;
        });
    },
    /**
     * 切换电站
     * **/
    changeEle() {
      this.ElecEqmIdFun();
    },
    /**
     * 切换电站下的设备
     * **/
    eqmChangeEle() {
      this.elecpoinFun();
    },
    /**
     * 获取园区下对应的配电房电站
     * **/
    elecstationFun() {
      elecstation()
        .then((res) => {
          const { errcode, errmsg, elecstation } = res;
          if (errcode == 0) {
            this.options = elecstation;
            if (this.options.length > 0) {
              this.value = this.options[0].elecStationId;
              this.ElecEqmIdFun();
            }
          } else {
          }
        })
        .catch((err) => {});
    },
    /**
     * 获取设备列表
     * **/
    // ElecEqmIdFun() {
    //   ElecEqmId(this.value)
    //     .then((res) => {
    //       const { errcode, errmsg, elecstation } = res;
    //       if (errcode == 0) {
    //         this.eqmOptions = elecstation;
    //         this.eqmValue = elecstation[0].elecEqmId;
    //         this.elecpoinFun(this.eqmValue);
    //       } else {
    //       }
    //     })
    //     .catch((err) => {});
    // },
    /**
     * 获取企业下对应的配电房运行数据
     * **/
    elecpoinFun() {
      this.eleLoad = true;
      this.elecpoinArry = [1,2,3,4,5,6,7,8,9];
      // elecpoin(this.eqmValue)
      //   .then((res) => {
      //     const { errcode, errmsg, content } = res;
      //     if (errcode == 0) {
      //       if (content) {
      //         this.elecpoinArry = content;
      //       }
      //     } else {
      //     }
      //     this.eleLoad = false;
      //   })
      //   .catch((err) => {
      //     this.eleLoad = false;
      //   });
    },
    /**
     * 获取测量点对应的值
     * **/
    getValueByName(name) {
      let value = "";
      if (this.elecpoinArry.length > 0) {
        this.elecpoinArry.forEach((item) => {
          if (item.elecPointUnitId == name) {
            value = this.toFixedTwo(item.dATA_VALUE);
          }
        });
      }

      return value;
    },
    /**
     * 改变能耗结构的局部loading状态
     * **/
    changeCHLoad(state) {
      this.chLoad = state;
    },
    /**
     * 改变光伏发电总览的局部loading状态
     * **/
    changeGenetateLoad(state) {
      this.genetateLoad = state;
    },
    /**
     * 数据保留两位小数
     * **/
    toFixedTwo(val) {
      if (val == 0) {
        return 0;
      }
      if (val) {
        //1.先判断是不是整数，如果不是整数则保留两位小数
        let num = Number(val);
        if (!this.$isInteger(val)) {
          num = num.toFixed(2);
        }
        return num;
      }
    },
  },
};
